import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchExpression,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../dependencies/empresaMFPDependency';
import { CLASS_NAME, ContribuicaoSindicalPatronal } from '../../../../models/dtos/mfp/contribuicaoSindicalPatronal';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'sindicato.codigo',
    order: OrderType.ASC,
  },
];

const ContribuicaoSindicalPatronalSearchView: FC<SearchViewProps<ContribuicaoSindicalPatronal>> = props => {
  const { globalParameter } = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  function getSearchExpression(): SearchExpression[] {
    return [
      {
        fieldName: 'empresaMFP.id',
        value: globalParameter.empresaMFP.id,
      },
    ];
  }

  const columns: ColumnsProps[] = [
    {
      dataField: 'sindicato.codigo',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Código',
    },
    {
      dataField: 'sindicato.razaoSocial',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Sindicato',
    },
  ];
  return (
    <WindowComponent
      title="Manutenção da Contribuiçao Sindical Patronal"
      dependencies={[() => hasEmpresa(globalParameter), () => hasEmpresaMFP(globalParameter)]}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        columns={columns}
        defaultSorted={defaultSorted}
        dtoClassName={CLASS_NAME}
        extraData={getExtraData(props, 'ContribuicaoSindicalPatronalEditView', CLASS_NAME)}
        searchExpressions={getSearchExpression}
      />
    </WindowComponent>
  );
};

export default ContribuicaoSindicalPatronalSearchView;
